<template>
  <div class="quiz_container">
    <h2>{{questions[current_question_index].question}}</h2>

    <ul class="answers">
        <Answer v-for="answer in questions[current_question_index].answers" v-bind:key="answer.id" v-bind:answer="answer" v-on:check_answer="check_answer($event)" :answer_visible="answers_visible"></Answer>
    </ul>
  </div>
</template>


<script>
import Answer from './Answer.vue'

export default {
  name: 'Quiz',
  components: {
    Answer,
  },
  data: function() {
      return {
          current_question_index: 0,
          answers_visible: false,
          questions: [{
            question: 'What\'s the fastest SUV around the Nurburgring?',
            answers: [{id: 0, answer: 'Mercedes AMG GLC 63 S',    is_correct: true},
                      {id: 1, answer: 'Porsche Cayenne Turbo',    is_correct: false},
                      {id: 2, answer: 'BMW X5 M',                 is_correct: false},
                      {id: 3, answer: 'VW Touareg V10 TDI',       is_correct: false}]
          },
            { question: 'What was the original engine size of the mk1 Golf GTI?', answers: [{answer: '1.2 litres', is_correct: false},
                                                                                            {answer: '1.4 litres', is_correct: false},
                                                                                            {answer: '1.6 litres', is_correct: true},
                                                                                            {answer: '1.8 litres', is_correct: false}]},
          { question: 'When was the Audi TT first on sale?', answers: [{answer: '1996', is_correct: false},
                                                                                    {answer: '1998', is_correct: true},
                                                                                    {answer: '2000', is_correct: false},
                                                                                    {answer: '2002', is_correct: false}]}, 
            { question: 'How much horsepower did the Trabant have?', answers: [{answer: '6bhp', is_correct: false},
                                                                                    {answer: '11bhp', is_correct: false},
                                                                                    {answer: '40bhp', is_correct: false},
                                                                                    {answer: '25bhp', is_correct: true}]},
            { question: 'Which German vehicle was this year\'s F1 safety car?', answers: [{answer: 'Mercedes SLS AMG', is_correct: false},
                                                                                    {answer: 'Mercedes AMG GT R', is_correct: true},
                                                                                    {answer: 'Mercedes AMG GT S', is_correct: false},
                                                                                    {answer: 'Opel Vectra 4x4', is_correct: false}]},
          ]        
      };
  },
  methods: {
      check_answer: function() {
        this.show_answers();
        setTimeout(() => this.hide_answers(), 2000);
        setTimeout(() => this.load_next_question(), 2000);
      },

      show_answers: function() {
        this.answers_visible = true;
      },

      hide_answers: function() {
        this.answers_visible = false;
      },

      load_next_question: function() {
          this.current_question_index = this.get_next_question_index()
      },

      get_next_question_index: function() {
        return (this.current_question_index + 1) % 5;
      },
  }
}
</script>

<style scoped>
    .quiz_container {
        padding: 5%;
        text-align: center;
    }

    ul {
        padding-inline-start: 0%;
    }
</style>
